/**
 * CallNativeService
 */
import { Injectable } from '@angular/core';

@Injectable()
export class CallNativeService {

    constructor() { }

    /**
     * @method postMessage
     * @param data {any}
     */
    private postMessage(data: any) {
        const TARGET_VIEW = 'mainView';
        try {
            const json: string = JSON.stringify(data);
            (<any>window).wizViewMessenger.postMessage(json, TARGET_VIEW);
        } catch (err) {
            console.error(err);
        }
    }

    /**
     * inAppBrowser
     * @method postMessage
     * @param args {IinAppBrowserArgs}
     */
    public inAppBrowser(args: IinAppBrowserArgs) {
        try {
            const data = {
                method: 'inAppBrowser',
                option: args
            };
            this.postMessage(data);
        } catch (err) {
            console.error(err);
        }
    }

    /**
     * localNotification
     * @method localNotification
     * @param args {IlocalNotificationArgs}
     */
    public localNotification(args: IlocalNotificationArgs) {
        try {
            const data = {
                method: 'localNotification',
                option: args
            };
            this.postMessage(data);
        } catch (err) {
            console.error(err);
        }
    }
}

/**
 * URL
 */
export enum InAppBrowserTarget {
    /**
     *  URL Cordova WebView InAppBrowser 
     */
    Self = '_self',
    /**
     * InAppBrowser 
     */
    Blank = '_blank',
    /**
     *  Web  ( systems web browser ) 
     */
    System = '_system'
}

/**
 * IinAppBrowserArgs
 */
export interface IinAppBrowserArgs {
    /**
     * URL
     */
    url: string;
    /**
     * URL Web 
     */
    target?: InAppBrowserTarget;
}

/**
 * localNotificationArgs
 */
export interface IlocalNotificationArgs {
    /**
     * ID
     */
    id: number;
    /**
     * 
     */
    title: string;
    /**
     * 
     */
    text: string;
    /**
     * 
     */
    trigger?: {
        /**
         * ISO
         */
        at: string
    };
    /**
     * 
     */
    icon?: string;
    /**
     * 
     */
    smallIcon?: string;
    /**
     * 
     */
    foreground?: boolean;
}

